import Vue from 'vue'
import Router from 'vue-router'
const lazyLoading = (name) => (resolve) => require([`../pages/${name}`], resolve)

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'main',
      component: lazyLoading('index'),
      children: [
        { path: '/index', component: lazyLoading('components/ymHome/ymContent'), name: 'index' },
        { path: '/order', component: lazyLoading('order'), name: 'order' },
        { path: '/patient', component: lazyLoading('patient'), name: 'patient' },
        { path: '/doctor', component: lazyLoading('doctor'), name: 'doctor' },
        { path: '/eva', component: lazyLoading('eva'), name: 'eva' },
        { path: '/package', component: lazyLoading('package'), name: 'package' },
        { path: '/sign', component: lazyLoading('sign'), name: 'sign' }
      ]
    },
    {
      path: '/',
      name: 'login',
      component: lazyLoading('login')
    }
  ]
})



