<template>
  <div id="app">
    <transition name="move-fade-right-to-left">
      <router-view class="app-router-view"></router-view>
    </transition>
  </div>
</template>
<script>
import 'vue-transition.css'
export default {
  name: 'app',
  components: {
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
*{
	padding: 0;
	margin: 0;
}
html{
	font-size: 6px;
}
body {
	background: #fff!important;
}
  html,
  body,
  .app {
    position: relative;
    width: 100%;
    height: 100%;
    /*overflow: hidden;*/
  }
  .app {
    background-color: #000;
    perspective: 1200px;
  }

  .app .app-router-view {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    overflow: hidden;
    backface-visibility: hidden;
    transform: translate3d(0, 0, 0);
    transform-style: preserve-3d;
    visibility: visible;
  }
  h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  /*display: inline-block;*/
  /*margin: 0 10px;*/
}
a {
  color: #42b983;
}
</style>



